@font-face {
  font-family: "IBM Plex Mono Light";
  src: url(./../../assets/fonts/IBMPlexMono/IBMPlexMono-Light.ttf) format("truetype");
  font-display: swap;
}
#home {
  position: relative;
  width: 100%;
  min-height: 100vh;
}
#home .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.65);
}
#home a {
  color: #fff;
}
#home p {
  margin-top: 0;
  margin-bottom: 6px;
  color: #fff;
  font-family: "IBM Plex Mono Light", sans-serif;
  font-style: normal;
  font-weight: 300;
  line-height: 43px;
}
#home .box {
  position: absolute;
}
#home .under-construction p {
  margin-left: -20px;
}
#home .first-col {
  width: 300px;
}
#home .last-col {
  width: 480px;
}

@media screen and (min-width: 1600px) {
  #home .box {
    left: 37%;
  }
}
@media screen and (min-width: 768px) and (max-width: 1599px) {
  #home .box {
    left: 30%;
  }
}
@media screen and (min-width: 1200px) {
  #home p {
    font-size: 33px;
  }
}
@media screen and (min-width: 992px) and (max-width: 1199px) {
  #home p {
    font-size: 24px;
  }
  #home .under-construction p {
    margin-left: -14px;
  }
}
@media screen and (min-width: 992px) {
  #home .box {
    bottom: 94px;
  }
  #home .under-construction {
    margin-bottom: 108px;
  }
}
@media screen and (min-width: 768px) and (max-width: 991px) {
  #home p {
    font-size: 18px;
  }
  #home .box {
    bottom: 94px;
  }
  #home .under-construction {
    margin-bottom: 27px;
  }
  #home .under-construction p {
    margin-left: -10px;
  }
}
@media screen and (min-width: 768px) {
  #home .overlay {
    margin: 95px 100px;
    overflow: hidden;
  }
  #home .logo-box {
    position: absolute;
    top: 11px;
    left: -45px;
  }
  #home .box {
    bottom: 20px;
    right: 0;
  }
}
@media screen and (max-width: 767px) {
  #home .overlay {
    overflow: auto;
  }
  #home .logo-box {
    display: block;
    text-align: center;
  }
  #home .box {
    right: 0;
    left: 0;
    padding: 40px 20px;
  }
  #home p {
    font-size: 16px;
  }
  #home .under-construction {
    margin-bottom: 54px;
  }
  #home .under-construction p {
    margin-left: -10px;
  }
}
body {
  margin: 0;
  background: url(./../../assets/images/bg.jpg) no-repeat fixed center/cover;
}

.flex {
  display: flex;
}